@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;500;700&display=swap);
.nav-left {
  grid-area: nav-left;
}
.header {
  grid-area: header;
}
.main {
  grid-area: main;
}

.container {
  display: grid;
  grid-template-areas:
    "nav-left header"
    "nav-left main";
  grid-template-columns: 18rem auto;
  grid-template-rows: 9rem auto;
}

.outlet-container {
  border: 1px solid #eee;
  padding: 1rem;
  background: #fff;
}

/* Stylesheet for left navigation */

.nav-left__container {
  width: 18rem;
  background: #3e3939;
  height: 100vh;
  box-shadow: 4px 0px 8px #000;
  position: fixed;
}

/* Company info and logo styles */
.company-info {
  height: 8rem;
  margin-top: 1rem;
  display: flex;
  align-items: center;
}

.logo {
  margin: 1rem;
}
.logo > img {
  width: 4rem;
}

h1,
h4 {
  font-weight: normal;
  color: #fff;
}

nav {
  font-size: 1.2rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

/* navigation link styles */
.link {
  text-decoration: none;
  display: block;
  height: 3rem;
  color: #0d0b0e;
  background: #64676a;
  padding: 1rem;
}

.link:hover,
.link:active,
.link:focus {
  color: #b7bdc2;
  background: #020202;
}

/* navigation bottom i.e, profile styles */
.nav-left__bottom {
  position: absolute;
  bottom: 0;
  width: inherit;
}

.nav-left__bottom > .link {
  background: #021718;
  color: #fff;
}

.nav-left__bottom > .link:hover,
.nav-left__bottom > .link:focus,
.nav-left__bottom > .link:active {
  color: #021718;
  background: #fff;
}

header {
  background: #f5f5f5;
  height: 9rem;
  box-shadow: 4px 0px 8px #000;
}
.top-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
}
.user {
  background: transparent;
  font-size: 1.5rem;
  text-decoration: none;
  color: #000;
  padding: 0.5rem 3rem;
}

.btn-log__out {
  cursor: pointer;
  padding: 1rem;
  margin: 0.5rem 3rem;
  font-size: 1rem;
  border: 1px solid #fcfcfc;
  border-radius: 10px;
  background: #3e3939;
  color: #ebebeb;
}

.bottom-header {
  margin: 0.6rem 0;
  display: flex;
  justify-content: space-evenly;
}

.bottom-header__nav {
  display: block;
  padding: 0.6rem;
  text-decoration: none;
  color: #fff;
  font-size: 1rem;
  border-radius: 4px;
}

.item-1 {
  background: #43bb4d;
}
.item-1:hover,
.item-1:active,
.item-1:focus {
  color: #43bb4d;
  background: #d2d8d7;
}

.item-2 {
  background: #ca2525;
}
.item-2:hover,
.item-2:active,
.item-2:focus {
  color: #ca2525;
  background: #d2d8d7;
}
.item-3 {
  background: #29bbd4;
}
.item-3:hover,
.item-3:active,
.item-3:focus {
  color: #29bbd4;
  background: #d2d8d7;
}
hr {
  width: 61rem;
  margin: 0 auto;
  border: 1px solid #ccc;
}

main {
  margin: 1rem;
}
.invoice-title {
  color: #000;
  font-weight: 700;
  font-size: 2rem;
  text-align: center;
}

.title {
  color: #000;
  text-align: center;
  font-weight: 700;
  font-size: 1.8rem;
  padding-top: 1rem;
}

table {
  margin: 0 auto;
}

.error {
  color: #fff;
  background: rgba(203, 56, 38, 0.8);
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
  padding: 0.5rem;
}

.spinning-loader {
  display: inline-block;

  width: 1.4rem;
  height: 1.4rem;

  border: 5px solid rgba(24, 41, 51, 0.2);
  border-left-color: rgb(11, 74, 114);

  border-radius: 50%;
  background: transparent;

  animation-name: rotate-s-loader;
  animation-iteration-count: infinite;
  animation-duration: 1s;
  animation-timing-function: linear;
  position: relative;
  top: 0.3rem;
  left: 1rem;
}

@keyframes rotate-s-loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}

.dashboard-container {
  height: calc(100vh - 11rem);
  display: flex;
  grid-gap: 4rem;
  gap: 4rem;
}

.list {
  margin-top: 2rem;
}

.list > h1 {
  color: #000;
  font-size: 1.4rem;
  font-weight: 700;
  margin-bottom: 2rem;
}

.list > ul > li {
  margin: 1rem;
}

.products-container {
  display: flex;
  width: 100%;
  justify-content: flex-start;
}

.products-container .link {
  background: #038f59;
  color: #fff;
  border-radius: 4px;
  margin-right: 2rem;
  margin-bottom: 2rem;
  box-shadow: 4px 4px 5px #282828;
}

.products-container .link:hover,
.products-container .link:active,
.products-container .link:focus {
  color: #038f59;
  background: #f8f8f8;
}

/* stylesheet for add product */

.form-container h1 {
  color: #000;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

label {
  font-weight: 700;
}
.form-item {
  display: grid;
}

input[type="text"],
input[type="email"] {
  height: 2rem;
  font-size: 1rem;
  margin-bottom: 1rem;
  border: 1px solid #c1c1c1;
  padding: 0 1rem;
}

input[type="text"]:focus,
input[type="email"]:focus {
  outline: 1px solid #43d9ff;
}

.btn {
  cursor: pointer;
  padding: 1rem;
  width: 20%;
  font-size: 1rem;
  font-weight: 700;
  border: none;
  border-radius: 5px;
  color: #fff;
}

.btn-add {
  background: #43d9ff;
}

.btn-clear {
  margin-left: 2rem;
  background: #ca2525;
}

/* stylesheet for view all products
 */

